:root {
  --transition: transform 0.5s ease;
}

.about__container {
  display: grid;
  grid-template-columns: 35% 50%;
  gap: 15%;
}

.about {
  text-align: center;
}

.about__me {
  width: 100%;
  aspect-ratio: 1/1;
  border-radius: 2rem;
  background: linear-gradient(45deg, rgb(87, 0, 128), silver, rgb(148, 100, 172));
  display: grid;
  place-items: center;
}

.about__me-image {
  height: 75%;
  width: 75%;
  border-radius: 2rem;
  overflow: hidden;
  transform: rotate(10deg);
  transition: var(--transition);
}

.about__me-image:hover {
  transform: rotate(0);
}

.about__cards {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1.5rem;
}

.about__card {
  background: black;
  font-size: 2rem;
  border: 1px solid transparent;
  border-radius: 1rem;
  padding: 2rem;
  text-align: center;
  transition: var(--transition);
}

.about__card:hover {
  background: url("data:image/jpeg;base64,");
  border-color: rgb(114, 13, 237);
  color: black;
  font-weight: bold;
  cursor: default;
}

.about__icon {
  color: rgb(114, 13, 237);
  font-size: 2.5rem;
  margin-bottom: 1rem;
}

.about__card small {
  font-size: 1.5rem;
  font-weight: bolder;
}

.about__content p {
  margin: 2rem 0 3rem;
  width: 100%;
  height:  30%;
  aspect-ratio: 1/1;
  border-radius: 2rem;
  background: linear-gradient(
    45deg,
    rgb(87, 0, 128),
    silver,
    rgb(148, 100, 172)
  );
  display: grid;
  place-items: center;
  color: black;
  font-weight: bolder;
  font-size: 1.3rem;
}

.about__content p .basecamp__link {
  color: black;
  font-weight: bolder;
  font-size: 1.3rem;
}

.about__content p .basecamp__link:hover {
  color: rgb(114, 13, 237);
  font-weight: bolder;
  font-size: 1.3rem;
  text-decoration: underline;
}


/* ============== MEDIA Med DEVICES ======= */

@media screen and (max-width: 1024px) {
  .about__container {
    grid-template-columns: 1fr;
    gap: 0;
  }

  .about__me {
    width: 50%;
    margin: 2rem auto 4rem;
  }
  

  .about__content {
    place-items: center;
  }
  .about__content p {
    margin: 5rem 4rem;
  }
}

/* ============== MEDIA Small DEVICES ======= */


@media screen and (max-width: 600px) {


  .about__cards {
    grid-template-columns: 1fr;
  }

  .about__card {
    font-size: 1.5rem;
    padding: 1.5rem;
  }
  .about__card small {
    font-size: 1rem;
  }

  .about__content p {
    margin: 4rem 2rem;
  }
}

/* ============== MEDIA EXTRA Small DEVICES ======= */

@media screen and (max-width: 400px) {
  .about__content p {
    margin: 2rem 1rem;
  }
}
